import React, { useEffect, useState } from 'react'

import './Achievements.css'

import icon from '../assets/icon.svg'
import icon1 from '../assets/icon-1.svg'
import icon2 from '../assets/icon-2.svg'
import icon3 from '../assets/icon-3.svg'

export default function Achievements() {
    const [client, setClient] = useState(0);
    const [branches, setBranches] = useState(0);
    const [projects, setProjects] = useState(0);
    const [work, setWork] = useState(0);

    // const getInterval = () =>

    useEffect(() => {

        let checkDeviceType =  /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)
          
        const interval = setInterval(() => {
            if ((checkDeviceType || client >= 1) && client < 60) {
                setClient((client) => client + 1);
            }

            if ((checkDeviceType || branches >= 1) && branches < 10) {
                setBranches((branches) => branches + 1);
            }

            if ((checkDeviceType || projects >= 1) && projects < 80) {
                setProjects((projects) => projects + 1);
            }

            if ((checkDeviceType || work >= 1) && work < 24) {
                setWork((work) => work + 1);
            }
    
        }, 30);
        return () => {
          clearInterval(interval);
        };
    }, [client, branches, work, projects]);


const onSetIntervals = () => {
    if ((client === 0)) {
        setClient(1)
        setWork(1)
        setBranches(1)
        setProjects(1)
    }
}



    return (
        <section onMouseOver={onSetIntervals} id="achievements" className="Achievements">
            <div className="card">
                 <figure>
                     <img src={icon} alt="icon" />
                 </figure>
                 <div>
                     <h2>{client}</h2>
                     <hgroup>

                     <h4>Statisfied</h4>
                     <h3>Client</h3>
                     </hgroup>
                 </div>
            </div>
            <div className="card">
                 <figure>
                     <img src={icon1} alt="icon" />
                 </figure>
                 <div>
                     <h2>{branches}</h2>
                     <hgroup>
                     <h4>Countrywide</h4>
                     <h3>Branches</h3>
                     </hgroup>
                 </div>
            </div>
            <div className="card">
                 <figure>
                     <img src={icon2} alt="icon" />
                 </figure>
                 <div>
                     <h2>{projects}</h2>
                     <hgroup>

                     <h4>Total</h4>
                     <h3>Projects</h3>
                     </hgroup>
                 </div>
            </div>
            <div className="card">
                 <figure>
                     <img src={icon3} alt="icon" />
                 </figure>
                 <div>
                     <h2>{work}</h2>
                     <hgroup>

                     <h4>Work</h4>
                     <h3>Finished</h3>
                     </hgroup>
                 </div>
            </div>
        </section>
    )
}
