import React, { useState } from 'react'

import './Projects.css'

import porject1 from '../assets/project_1.webp'
import porject2 from '../assets/project_2.webp'
import porject3 from '../assets/project_3.webp'

export default function Projects() {
    const [toggle, setToggle] = useState(false)
    const onToggle = (value) => {
        setToggle(value)
    }
    return (
        <section id="projects" className="Projects">
            <h2>Our Projects</h2>
            <ul>
                <li style={{color: !toggle && "#68d1cb"}} onClick={() => onToggle(false)} >All</li>
                <li style={{color: toggle === "building" && "#68d1cb"}} onClick={() => onToggle("building")} >Building</li>
                <li style={{color: toggle === "rebuild" && "#68d1cb"}} onClick={() => onToggle("rebuild")} >Rebuild</li>
                <li style={{color: toggle === "architecture" && "#68d1cb"}} onClick={() => onToggle("architecture")} >Architecture</li>
            </ul>
           {!toggle && <div>
                <img src={porject1} alt="project name" />
                <img src={porject2} alt="project name" />
                <img src={porject3} alt="project name" />
            </div>}
            {toggle === "building" && <div>
                <img src={porject1} alt="project name" />
                <img src={porject2} alt="project name" />
                <img src={porject3} alt="project name" />
                <img src={porject3} alt="project name" />
            </div>}
            {toggle === "rebuild" && <div>
                <img src={porject1} alt="project name" />
                <img src={porject2} alt="project name" />
                <img src={porject3} alt="project name" />
            </div>}
            {toggle === "architecture" && <div>
                <img src={porject1} alt="project name" />
                <img src={porject2} alt="project name" />
                <img src={porject3} alt="project name" />
            </div>}
        </section>
    )
}
