import React, { useState } from "react";

import "./AppHeader.css";

import logo from "../assets/logo-light.png";
import menuIcon from "../assets/menu-icon.svg";
import menuClose from "../assets/menu-close.svg";
// import arrowDown from "../assets/arrow-down.svg";

export default function AppHeader() {
  const [toggle, setToggle] = useState(false)
  return (
    <header className="App-Header">
        <h1>
          <span>GBC Greenpoint</span>
          <img src={logo} alt="logo" />
        </h1>
      <nav>
      {!toggle &&  <img src={menuIcon} onClick={() => setToggle(!toggle)} alt="menu" />}
      {toggle &&  <img src={menuClose} onClick={() => setToggle(false)} alt="close menu" />}
    <ul style={{display: toggle ? "inline-block" : "none"}}>
          <li><a href="#about" onClick={() => setToggle(false)} >About</a></li>
          <li><a href="#services" onClick={() => setToggle(false)} >Services</a></li>
          <li><a href="#team" onClick={() => setToggle(false)} >Team</a></li>
          <li><a href="#testimonies" onClick={() => setToggle(false)} >Testimonies</a></li>
          <li><a href="#projects" onClick={() => setToggle(false)} >Projects</a></li>
          {/* <li><a href="#home" onClick={() => setToggle(false)} >more  <img src={arrowDown} alt="more" /></a></li> */}
          <li><a href="#contacts" onClick={() => setToggle(false)} >Contact</a></li>
        </ul>
        <button>Get a quote</button>
      </nav>
    </header>
  );
}
