import React, { useEffect, useState } from 'react'

import './Testimonies.css'

import avatar from '../assets/avatar.jpg'

export default function Testimonies() {
    const [slide, setSlide] = useState(1)

    useEffect(() => {
        const interval = setInterval(() => {
          setSlide((slide) => slide + 1);
    
          if (slide === 2) {
            setSlide(1);
          }
        }, 8000);
        return () => {
          clearInterval(interval);
        };
      }, [slide]);

    return (
        <section id="testimonies" className="Testimonies">
          <div>

            <h2>Some Feedback From Client</h2>
            <p>Which cattle fruitful he fly visi won not let above lesser stars fly form wonder every let third form two air seas after us said day won lso together midst two female she</p>
          </div>
            <div>
            <div style={{display : slide === 1 ? "inline-block" : "none"}} className="card">
                <figure>
                 <img src={avatar}  alt="avatar" />
                </figure>
                <div>
                <p> Life open fifth midst lesser place light after unto move that make had void and whales. So after void called whose were cattle fourth seed Image yielding is given every own tree Image</p>
                <h4>John Vuli'igate, Johannesburg</h4>
                </div>
            </div>
              <div style={{display : slide === 2 ? "inline-block" : "none"}} className="card">
                <figure>
                 <img src={avatar}  alt="avatar" />
                </figure>
                <div>
                <p> Life open fifth midst lesser place light after unto move that make had void and whales. So after void called whose were cattle fourth seed Image yielding is given every own tree Image</p>
                <h4>Piet Vuli'igate, Johannesburg</h4>
                </div>
            </div>
              {/* <div style={{display : slide === 3 ? "inline-block" : "none"}} className="card">
                <figure>
                 <img src={avatar}  alt="avatar" />
                </figure>
                <div>
                <p> Life open fifth midst lesser place light after unto move that make had void and whales. So after void called whose were cattle fourth seed Image yielding is given every own tree Image</p>
                <h4>Hendrick Vuli'igate, Johannesburg</h4>
                </div>
            </div> */}
            </div>
            <ul>
          <li tabIndex="0" style={ slide === 1 ? {backgroundColor: "#0e1a29",
        transition: "ease-in 1s"} : null} onClick={() => setSlide(1)}></li>
          <li tabIndex="1" style={ slide === 2 ? {backgroundColor: "#0e1a29",
        transition: "ease-in 1s"} : null} onClick={() => setSlide(2)}></li>
          {/* <li tabIndex="2" style={ slide === 3 ? {backgroundColor: "#0e1a29",
        transition: "ease-in 1s"} : null} onClick={() => setSlide(3)}></li> */}
        </ul>
         </section>

    )
}
